				"html": "<p>Getting started with <a href=\"//laktek.github.io/punch/\">Punch</a> is very easy. After adding the <a href=\"//www.npmjs.com/package/punch\">npm plugin</a>, setting up the initial static site takes a few minutes. The CLI commands are super straight forward &#x26; the site re-builds super fast.</p>\n<h2>Setup</h2>\n<p>In terminal run:</p>\n<ul>\n<li>\n<p><code>npm install -g punch</code> Install punch global</p>\n</li>\n<li>\n<p><code>punch setup [my awesome site name]</code> *site setup</p>\n</li>\n<li>\n<p><code>cd mysite</code> </p>\n</li>\n<li>\n<p><code>punch s</code> starts your local server</p>\n</li>\n<li>\n<p>Press <code>ctrl + c</code> to stop your local server</p>\n</li>\n<li>\n<p>If you have already have a directory setup, just run <code>punch setup</code> in <em>that</em> directory</p>\n</li>\n</ul>\n<h2>An aside: Why Punch?</h2>\n<p>Here are 2 big benefits for choosing punch specifically over other static site generators.</p>\n<ul>\n<li><em>Quick setup</em>: you really can be up &#x26; running in a few minutes</li>\n<li><em>Prototyping</em>: you can quickly do templating for platform cms sites where you can use dummy <a href=\"//www.json.org/\">json</a>. This is great in a situation where multiple CMS's are on the table that use <a href=\"//mustache.github.io/mustache.5\">mustache</a>.html)</li>\n</ul>\n<h2>More information</h2>\n<ul>\n<li>Here's a link to the <a href=\"//github.com/laktek/punch/wiki\">wiki</a>, &#x26;</li>\n<li>Another link to the <a href=\"//laktek.github.io/punch/\">homepage</a></li>\n</ul>",
